body {
  margin: 0;
  padding: 0;
  min-width: 290px;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: #000000;
  background-color: white;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

p, ul {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

